<template>
  <Banner></Banner>


  <my-list></my-list>

  <!--    -->
  <div class="scoll-menu">
    <img :src="require(`@/assets/images/scroll_up.png`)" v-on:click="scrollUp"/>

  </div>
</template>

<script>
import Banner from "@/components/banner/Banner";
import MyList from "./MyList";

export default {
  name: 'MyListHome',
  components: {MyList, Banner},
  data: () => ({
    username: '',
    password: '',
    url: '',
    input: '', //  
    msg: '',   //   
    views : '0', //  ,
  }),
  created() {
    this.username = this.$cookies.get('username');
    this.url = this.resourceHost + '/history';
  },
  methods: {
    scrollUp(){
      window.scrollTo(0,0);
    },
  }
}
</script>
<style>

    .my-list {
        height:200px;
    }
</style>